const COLORS = ["#fee9e9", "#e1f5fe", "#e8f6ed"];
export default {
  data() {
    return {
      value: '',
      colors: COLORS,
      planList: [
        {
          id: 1,
          name: "未开工",
          type: 1,
          currentPlan: 1,
          list: [{ label: "征地", value: 1 }, { label: "图审", value: 2 }]
        },
        {
          id: 2,
          name: "未开工",
          type: 2,
          currentPlan: 2,
          list: [{ label: "征地", value: 1 }, { label: "图审", value: 2 }]
        },
        {
          id: 3,
          name: "未开工",
          type: 3,
          currentPlan: 1,
          list: [{ label: "征地", value: 1 }, { label: "图审", value: 2 }]
        }
      ],
      dialogImageUrl: '',
      dialogVisible: false,
      tableHistory: [{}],
      dialogTime: false,
      projectId: this.$route.params.projectId,
      schoolId: this.$route.params.schoolId,
      schoolScheduleId: this.$route.params.schoolScheduleId,
      edit: this.$route.params.edit,
      townId: sessionStorage.getItem("townId"),//区县id  100001
      schuledlist: [],
      form: {},
      fileList: [],
      fileStr: '',
      invest: '',
      nowSchedule: '',
      reward: '',
			daopaiList: [],
      
    }
  },

	computed: {
		fileLists() {
  		let newArr = [];
  		this.fileList.forEach((item, index) => {
  			newArr.push({
  				name: item.fileName,
  				url: item.fileUrl
  			})
  		})
  		return newArr;
  	}
	},

  mounted() {
		this.$nextTick(() => {
    	this.init();//查询项目数据
			this.queryDaopaishijian();//查询倒排时间
    	
    });
  },

  methods: {
  	init(){
  		
  		let temp = this;
			
			//查询申报信息
			if(this.schoolScheduleId!=null && this.schoolScheduleId!=''){
				this.$post('/web/findSchoolSchedule', {
					"id":this.schoolScheduleId,
					}).then((m)=> {
					if(m.resultCode == 0){
						var tSchoolSchedule = m.content.tSchoolSchedule;
						temp.nowSchedule = tSchoolSchedule.scheduleId+"";
						temp.invest = tSchoolSchedule.invest;
						temp.reward = tSchoolSchedule.reward;
						
						temp.fileList = m.content.fileList;
//						console.log(temp.fileList);
					}
				})
			}
			
			//查询项目下阶段
			this.$post('/web/queryProjectSchedule', {
				"projectId": this.projectId,
				"townId": this.townId,
				}).then((m)=> {
				if(m.resultCode == 0){
					let list = m.content.list, same = [];
					
					list.forEach((item, index) => {
						if( !!same.length ) {
							if( item.id === same[same.length-1].id ) {
								same[same.length-1].list.push(item);
							}else {
								item.list = [item];
								same.push(item)
							}
						}else {
							item.list = [item];
							same.push(item);
						}
					})
					temp.schuledlist = same;
				}
			})
  		
  	},
  	save(){
  		let temp = this;
  		this.fileStr = JSON.stringify(this.fileList);
			if(this.invest == ''){
				this.$vux.toast.show({
					text: '本月投入不能为空!',
					width: '3rem'
				})
				return;
			}else if(isNaN(this.invest)){
				this.$vux.toast.show({
					text: '本月投入只能是数字!',
					width: '3rem'
				})
				return;
			}
  		this.$post('/web/saveSchoolSchedule', {
				"scheduleId":this.nowSchedule,
				"schoolId":this.schoolId,
				"invest": this.invest,
				"reward": this.reward,
				"fileStr": this.fileStr,
				"id": this.schoolScheduleId
				}).then((m)=> {
					this.$vux.toast.show({
						text: '申报成功!',
						width: '3rem'
					})
					this.quxiao();
			})
  		
  	},
  	queryDaopaishijian(){
  		
  		let temp = this;
			this.$post('/web/queryDaopaishijian', {
				"schoolId":temp.schoolId
				}).then((m)=> {
				if(m.resultCode == 0){
					temp.daopaiList = m.content.list;
				}
			})
  		
  	},
  	changeState(row){
			let temp = this;
			this.$post('/web/changeDaopaishijian', {
				"id":row.id
				}).then((m)=> {
				if(m.resultCode == 0){
					temp.$vux.toast.show({
						text: '状态更新成功!',
						width: '3rem'
					})
				}
			})
			
		},
    handleRemove(file, fileList) {
			var index = this.fileLists.indexOf(file);
			this.fileLists.splice(index,1);
			this.fileList.splice(index,1);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url || file;
      this.dialogVisible = true;
    },
    newhandlesuccess(file,fileList){
//  	console.log('newhandlesuccess',file);
    	if(file.resultCode == 0){
				this.fileList.push({"fileName":file.content.fileName,"fileUrl": file.content.fileUrl});
//  	  console.log('newhandlesuccess',this.fileList);
    	}else if(file.resultCode == 'tolarge'){
    		this.$vux.toast.show({
					text: '文件大小不能超过50M!',
					width: '3rem'
				})
    		var index = this.fileLists.indexOf(file);
				this.fileLists.splice(index,1);
				this.fileList.splice(index,1);
				return;
    	}
	  },
    quxiao(){
    	localStorage.setItem("projectId", this.projectId);
			this.$router.push({name: 'AppCountySchool'});
		},
    
  }
}



